<template>
  <v-card
    height="100%"
  >
    <div
      id="nodocentes-concurso"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Cargos concursados.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('nodocentes-concurso')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as No Docentes.</v-card-subtitle>

      <v-card-text>
        <v-responsive>
          <GChart
            :data="chartData"
            :options="chartOptions"
            type="PieChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Concursado
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in concursadoData"
                :key="index"
              >
                <td>{{ parseConcursado(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['nodocentesCargos', 'mobileSize']),
    chartData () {
      return [
        ['Concursado', 'Cargos'],
        ['Si', this.concursadoData.si],
        ['No', this.concursadoData.no]
      ]
    },
    chartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        backgroundColor: { fill: 'transparent' },
        height: 350,
        bar: { groupWidth: '70%' },
        pieHole: '0.2',
        sliceVisibilityThreshold: 0.0001,
        legend: {
          position: 'right',
          alignment: 'center',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    concursadoData () {
      return this.nodocentesCargos.reduce((acc, cur) => {
        return cur.cargo_concursado === 'SI'
          ? { ...acc, si: acc.si + 1 }
          : cur.cargo_concursado === 'NO'
            ? { ...acc, no: acc.no + 1 }
            : acc
      },
      {
        si: 0,
        no: 0
      }
      )
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    parseConcursado (v) {
      return v === 'si'
        ? 'Si'
        : v === 'no'
          ? 'No'
          : 'No declara'
    }
  }
}
</script>
